import React, { Component } from 'react';
import data from './Card Data/data.json';

class otherResources extends Component {  
  constructor() {
    super()
    this.state = {
      knownLinks: ["www.youtube.com"],
    }
    this.resourceClicked = this.resourceClicked.bind(this)
  }
  
  resourceClicked(resourceClicked) {
    console.log(resourceClicked.target.id)
    if (!(resourceClicked.target.className ==="resourceTitle")) {
      console.log("doing")
      this.props.history.push("/Other_Resources/"+resourceClicked.target.id.replace(/\s/g, '_'))
    }
  }

  render() {
    return (
      <div className="otherResources">
        <div className = "arrowHolder">
          <i class="fas fa-arrow-left" onClick = {this.props.history.goBack}/>
        </div>
        <div className = "allResources">
          {data.cards.map((currentCard) => {
            return (
              <div 
                className = "resource"
                onClick = {this.resourceClicked}
                id ={currentCard.title}
              >
                <div className = "TitleSection" id ={currentCard.title}>
                  <h1 className = "resourceTitle">{currentCard.title}</h1>
                  <img 
                    src = {currentCard.image} 
                    alt = "card preview" 
                    className = "resourceImage" 
                    id ={currentCard.title}
                  />
                </div>
                <div className = "ContentSection" id ={currentCard.title}>
                  {Object.values(currentCard.resources).map((currentResource) => {
                    let displayedIcon;
                    const currentURL = new window.URL(currentResource).host

                    if (this.state.knownLinks.includes(currentURL)) {
                      if (currentURL === "www.youtube.com") {
                        displayedIcon = <i class="fab fa-youtube" id ={currentCard.title}></i> 
                      }
                    }
                    else {
                      displayedIcon = <i class="fas fa-link" id ={currentCard.title}></i>
                    }         

                    return (
                      <div className = "displayedIcons" id ={currentCard.title}>
                        {displayedIcon} 
                      </div>
                      
                    )
                  })}
                </div> 
              </div>            
            )
          })}            
        </div>
      </div>
    );
  }
}

export default otherResources;