import React, { Component } from 'react';
import './styles/allStyles.scss';
import { Link } from 'react-router-dom'


class Cards extends Component {  
  constructor() {
    super()
    this.state = {
      cardOpen: false,
    }
  }

  render() {

    return (
      <div className = "allCards">
        {this.props.renderedCards.map((currentCard) => {
          let cardDesription;
          if (currentCard.description.length >= 100) {
            cardDesription = currentCard.description.substring(0,100) + " . . ."
          }
          else {
            cardDesription = currentCard.description
          }
          return (
            <div key= { currentCard.id } className = "Card" >
              <div className = "Card_Topbar"> 
                <button className = "Card_interact_item" >
                  <i className="fas fa-share" aria-hidden="True"></i>
                  {/* Visibility hidden in css */}
                </button>
                <h1 className = "Card_title" >{ currentCard.title.replace(/_/g, " ") }</h1>
                <button className = "Card_interact_item" >
                  <i className="fas fa-save" aria-hidden="True"></i>
                  {/* Visibility hidden in css */}
                </button>
              </div>
              
              <Link 
                className = "CardimageHolder" 
                to = {{pathname: "/"+currentCard.title.replace(/\s/g, '_')}}
                >
                <img
                  id = { currentCard.title }
                  className = "Card_image" 
                  src = { currentCard.image }
                  alt="Card Preview"                    
                />                  
              </Link>

                <div className = "Card_text" >
                  <p className = "Card_description">{ cardDesription }</p>
                </div>
            </div>
          )
        })}
      </div>
    );
  }
}

export default Cards;