import React, { Component } from 'react';
import HomeScreen from './homeScreen';
import CardContent from './cardContent';
import SavedCards from './savedCards'
import OtherResources from './otherResources'
import RequestACard from './requestACard'
import OtherResourceContent from './otherResourcesContent'
import NoCard from './noCard'
import { BrowserRouter, Route, Switch} from 'react-router-dom';
// import { TransitionGroup, CSSTransition } from 'react-transition-group'
import data from './Card Data/data.json';

class App extends Component {  
  render() {
    let allCardNames = [];
    data.cards.forEach((card) => {
    allCardNames.push(card.title.replace(/\s/g, '_'))
    });
    
    return (
      <div className="App">
        <BrowserRouter basename="/Calc-Cards">
          {/* <Route render={({location}) => (
            <TransitionGroup>
              <CSSTransition
                key={location.key}
                timeout={300}
                classNames="fade"
                appear={true}
              > */}
                <Switch>
                  <Route path = "/" component = {HomeScreen} exact/>
                  <Route path = "/Saved_Cards" component = {SavedCards} exact/>
                  <Route path = "/Other_Resources" component = {OtherResources} exact/>
                  <Route path = "/Request_a_Card" component = {RequestACard} exact/>
                  
                  {allCardNames.map((cardName) => {
                    return <Route key = {cardName} path = {"/"+cardName} component = {CardContent} exact/>
                    
                  })}
                  
                  {allCardNames.map((cardName) => {
                    return <Route key = {cardName} path = {"/Other_Resources/"+cardName} component = {OtherResourceContent} exact/>

                  })}

                  <Route component = {NoCard}/>
                </Switch>
               {/* </CSSTransition>
             </TransitionGroup>          
          )}/> */}
        </BrowserRouter>
      </div>
    );
  }
}

export default App;